import React, { useState } from 'react';
import uuid from 'react-uuid'
//import './App.css';
import './Style.scss';

function App() {

  const [sdata, setSdata] = useState([]);
  const [datas, setDatas] = useState(['0-10', '11-20', '21-30', '31-40', '41-50', '51-60', '61-70', '71-80', '81-90']);

  const handleClick = (e, index) => {
    setSdata([...sdata, e.target.innerHTML]);
    var list = [...datas]
    list.splice(index, 1);
    setDatas(list);
    console.log(index);
  }
  const handleSelected = (e, index) => {
    setDatas([...datas, e.target.innerHTML]);
    var available_list = [...sdata]
    available_list.splice(index, 1);
    setSdata(available_list);
    //console.log(index);
  }
  const selectallhandler = () => {
    const final = [...sdata].concat([...datas]);
    console.log(final);
    setDatas([]);
    setSdata(final);
  }
  const handleClear = () => {
    const final = [...datas].concat([...sdata]);
    console.log(final);
    setSdata([]);
    setDatas(final);

  }
  return (
    <div className="side">
      <div className="container">
        <h2 className="heading">Available List</h2>
        <button onClick={selectallhandler}> Select All</button>
        <div className="available">
          {datas.map((data, index) =>
            <div id="click" key={uuid()} onClick={(e) => handleClick(e, index)} value={data} className="highlighted_1">
              {data}
            </div>
          )}
        </div>
      </div>


      <div className="container">
        <h2 className="heading">Selected List</h2>
        <button onClick={handleClear}> Clear All</button>

        <div className="selected">

          {sdata.map((item, index) =>
            <div id="select" onClick={(e) => handleSelected(e, index)} className="highlighted_2" key={uuid()}>
              {item}
            </div>
          )}
        </div>
      </div>
    </div>

  );
}

export default App;
